.footer {
  padding: 200px 0;
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 104px;
  }
  &__info {
    max-width: 400px;
  }
  &__desc {
    font-weight: 500;
    font-size: 25px;
    line-height: 38px;
    color: var(--limeGreen-color);
  }
}
.form {
  max-width: 1110px;
  width: 100%;
  border: 1px solid #486958;
  padding: 35px 55px;
  &__input {
    width: 100%;
    margin-bottom: 145px;
    &:nth-child(2) {
      margin-bottom: 0px;
    }
    &-item {
      border: none;
      width: 100%;
      border-bottom: 1px solid #000000;
      padding: 0 0 25px 0;
      font-weight: 500;
      font-size: 25px;
      line-height: 38px;
      color: var(--limeGreen-color);
      &::placeholder {
        font-weight: 500;
        font-size: 25px;
        line-height: 38px;
        color: var(--limeGreen-color);
      }
      &:focus {
        outline: 0;
      }
    }
  }
  &__btn {
    display: flex;
    padding: 21px 73px;
    background: var(--darkCyan-color);
    border-radius: 20px 20px 20px 0px;
    font-weight: 700;
    font-size: 25px;
    line-height: 38px;
    color: var(--light-color);
    border: none;
    cursor: pointer;
    float: right;
    margin-top: 116px;
  }
}
@include tablet {
  .footer {
    padding: 50px 0;
  }
  .footer__container {
    justify-content: center;
    flex-direction: column;
  }
}
