.hero {
  padding: 140px 0 0 0;
  &__container {
    display: flex;
    justify-content: space-between;
  }
  &__desc {
    max-width: 711px;
  }
  &__title {
    color: var(--dark-color);
    margin-bottom: 50px;
  }
  &__text {
    margin-bottom: 50px;
  }
  &__img {
    background:  var(--lightGreen-color);
    border-radius: 120px;
    display: flex;
    justify-content: center;
    max-width: 630px;
  }
}
@media (max-width: 1300px) {
  .hero {
    padding: 50px 0 0 0;
  }
  .hero__container {
    flex-direction: column;
  }
  .hero__desc {
    margin-bottom: 50px;
  }
  .hero__img {
    margin: 0 auto;
  }
}
