.works {
  padding: 120px 0 50px 0;
  &__header {
    text-align: center;
    max-width: 506px;
    margin: 0 auto;
    margin-bottom: 70px;
  }
  &__title {
    margin-bottom: 20px;
  }
}
.video {
  display: flex;
  justify-content: space-between;
  border-radius: 10px 0px 0px 10px;
  background: var(--lightGreen-color);
  margin-bottom: 150px;
  &-img {
    img {
      height: 100%;
    }
  }
  &__info {
    max-width: 550px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
    padding: 0 100px;
  }
  &__desc {
    max-width: 328px;
    min-width: 320px;
    &-text {
      margin: 0;
      font-weight: 500;
      font-size: 20px;
      line-height: 30px;
      color: var(--limeGreen-color);
      margin-bottom: 40px;
    }
    &-link {
      font-weight: 700;
      font-size: 20px;
      line-height: 30px;
      color: var(--limeGreen-color);
      text-transform: uppercase;
      display: inline-block;
    }
  }
}
.video__play {
  position: absolute;
  padding: 29px 30px 29px 43px;
  border-radius: 65px;
  background: var(--light-color);
  left: -51px;
  &::after {
    content: "";
    position: absolute;
    width: 128px;
    height: 128px;
    background: rgba(255, 255, 255, 0.4);
    border-radius: 65px;
    top: -13px;
    left: -13px;
  }
}
.mission {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  &__desc {
    max-width: 708px;
  }
  &__title {
    margin: 0;
    max-width: 487px;
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    color: var(--dark-color);
    margin-bottom: 50px;
  }
  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 30px;
    line-height: 45px;
    color: var(--limeGreen-color);
  }
}
@media (max-width: 1300px) {
  .mission {
    flex-direction: column;
    gap: 0;
  }
  .video{
    margin-bottom: 50px;
  }
  .mission__img {
    order: 2;
    margin: 0 auto;
    margin-top: 50px;
  }
  .mission__title {
    font-size: 26px;
    line-height: 50px;
  }
  .mission__text {
    font-size: 17px;
    line-height: 32px;
  }
}
@include small-tablet {
  .video {
    flex-direction: column;
  }
  .video__info {
    max-width: none;
    padding: 70px 15px;
  }
  .video__play {
    top: -65px;
    left: 50%;
    transform: translateX(-50%);
  }
  .video__desc {
    min-width: 0px;
  }
}
