.btn {
  background: var(--darkCyan-color);
  padding: 21px 83px 21px 90px;
  border-radius: 20px 20px 20px 0px;
  font-weight: 700;
  font-size: 25px;
  line-height: 38px;
  display: inline-block;
  color: var(--light-color);
}
.title {
  margin: 0;
  font-weight: 800;
  font-size: 70px;
  line-height: 105px;
}
.text {
  margin: 0;
  font-weight: 400;
  font-size: 30px;
  line-height: 45px;
}
@include mobile {
  .title {
    font-size: 42px;
    line-height: 74px;
  }
  .text {
    font-size: 20px;
    line-height: 40px;
  }
}
