.testimonial {
  &__title {
    text-align: center;
    margin-bottom: 95px;
  }
  &__company {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 90px;
    margin-bottom: 115px;
  }
}
.about__testimonial {
  display: flex;
  &-img {
    max-width: 615px;
    min-width: 290px;
    img {
      height: 100%;
    }
  }
  &-content {
    background: var(--lightGreen-color);
    display: flex;
    flex-direction: column;
    padding: 50px 115px;
  }
  &-info {
    display: flex;
    gap: 117px;
    margin-bottom: 115px;
  }
  &-text {
    max-width: 752px;
    font-weight: 500;
    font-size: 25px;
    line-height: 38px;
    color: var(--limeGreen-color);
    &__span {
      display: block;
      margin-top: 50px;
      color: var(--darkCyan-color);
    }
  }
}
@include tablet {
  .about__testimonial {
    flex-direction: column;
  }
  .about__testimonial-img {
    max-width: none;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .about__testimonial-info {
    margin-bottom: 20px;
  }
  .about__testimonial-content {
    padding: 20px;
  }
}
@include small-tablet {
  .testimonial__title {
    margin-bottom: 50px;
  }
  .about__testimonial-info {
    gap: 35px;
  }
  .about__testimonial-text {
    font-size: 18px;
    line-height: 32px;
  }
}
