.project {
  padding: 150px 0;
  &__header {
    text-align: center;
  }
  &__title {
    margin: 0;
    margin-bottom: 20px;
  }
  &__sub-title {
    margin: 0;
    position: relative;
    margin-bottom: 160px;
    &::after {
      content: "";
      position: absolute;
      display: block;
      left: 50%;
      transform: translateX(-50%);
      bottom: -50px;
      width: 135px;
      height: 4px;
      border: 5px solid var(--darkCyan-color);
    }
    &::before {
      content: "";
      position: absolute;
      display: block;
      left: 50%;
      transform: translateX(-50%);
      bottom: -75px;
      width: 135px;
      height: 4px;
      border: 5px solid var(--darkCyan-color);
    }
  }
  &__row {
    display: flex;
    justify-content: center;
    gap: 50px;
  }
  &__colum {
    max-width: 450px;
    display: flex;
    flex-direction: column;
    gap: 35px;
    &:nth-child(2) {
      flex-direction: column-reverse;
    }
    &-title {
      margin: 0;
      font-weight: 700;
      font-size: 40px;
      line-height: 60px;
      color: var(--darkCyan-color);
      margin-bottom: 5px;
    }
    &-desc {
      margin: 0;
      font-weight: 500;
      font-size: 20px;
      line-height: 30px;
      color: var(--limeGreen-color);
      flex: 1 1 auto;
    }
  }
}
@include big-desktop {
  .project {
    padding: 50px 0;
  }
  .project__row {
    flex-wrap: wrap;
    align-items: center;
  }
  .project__colum {
    margin-bottom: 50px;
  }
  .project__colum:nth-child(2) {
    flex-direction: column;
  }
}
