.header {
  padding: 95px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    display: flex;
    align-items: center;
    z-index: 10;
    &-text {
      font-weight: 800;
      font-size: 35px;
      line-height: 52px;
      color: #58a9a5;
      margin-left: 14px;
    }
  }
}
.nav {
  display: flex;
  align-items: center;
  &__list {
    display: flex;
  }
  &__item {
    margin-left: 85px;
  }
  &__link {
    font-weight: 400;
    font-size: 25px;
    line-height: 38px;
    color: var(--lightDark-color);
    transition: 0.3s;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    &::after {
      content: "";
      position: absolute;
      background: #fec62d;
      top: 37px;
      border-radius: 21px;
      transition: 0.3s;
      width: 0px;
      height: 6px;
    }
    &:hover {
      color: var(--darkCyan-color);
    }
    &:hover::after {
      width: 48px;
    }
  }
  &__btn {
    padding: 15px 75px;
    background: var(--darkCyan-color);
    border: none;
    border-radius: 15px;
    margin-left: 220px;
    font-weight: 500;
    font-size: 25px;
    line-height: 38px;
    color: var(--light-color);
    cursor: pointer;
  }
}
.burger {
  display: none;
  @include burger;
}
@media (max-width: 1300px) {
  .header {
    padding: 10px 0;
  }
  .burger {
    display: block;
  }
  .nav {
    transform: translateX(150%);
    padding-top: 100px;
    transition: 0.5s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    background-color: var(--light-color);
  }
  .nav__btn {
    margin-top: 40px;
    margin-left: 0;
  }
  .nav__item {
    text-align: center;
    margin-bottom: 40px;
    margin-left: 0;
    &:last-child {
      margin-bottom: 0px;
    }
  }
  .nav__list {
    flex-direction: column;
  }
  .menu--active {
    transform: translateX(0);
    transition: 0.5s;
  }
}
